import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom';

import Switch from '@material-ui/core/Switch';

import { ESysBlocks } from '../components/EngSystems/MainPageESys';
import { BoilerESys } from '../components/EngSystems/BoilerESys';


const MainMenuLink2 = (props) => (
  <h4 className="mx-2">
    <Link to={props.url}>{props.title}</Link>
  </h4>
)

const MainMenuLink = (props) => (
    <div className="col-3 ws-home">
      <Link to={props.url}>{props.title}</Link>
    </div>
)


class MainPage extends Component {
   constructor(props) {
    super(props);

    this.state = {
      eng: false,
      tabs: [
        {name: 'mainTab',   active: true, title: 'Главная'},
        {name: 'boilerTab', active: false, title: 'Котельная'},
        {name: 'pumpsTab', active: false, title: 'Насосная'},
      ],
    }
    this.getActiveTab = this.getActiveTab.bind(this);
    this.setTab = this.setTab.bind(this);
  }

  componentDidMount() {
    if (this.props.user.is_officer) {
      this.setState({...this.state, eng: true})
      this.props.getESystemsData()
      this.props.getMainPageReport()
    }
  }

  setTab (tab) {
    let { tabs } = this.state
    tabs.map((tb) => {
      tb.active = false
      if (tb.name === tab.name)
        tb.active= true
    })

    this.setState({
      tabs: tabs
    })
  }

  getActiveTab () {
    let { tabs } = this.state
    let activeTab = {}
    tabs.map(tb => {
      if (tb.active)
        activeTab = tb
    })

    return activeTab
  }
  
  render() {
    const activeTab = this.getActiveTab()
    const { population, eData } = this.props
    const { eng } = this.state
    return (
        <div className='container-fluid'>
          <div className='d-flex justify-content-between align-items-baseline mt-2'>
            <h1>Свинокомплекс Николаевский</h1>
            <div className='my-0'>
              <label>Инженерные системы</label>
              <Switch
                checked={this.state.eng}
                onChange={() => this.setState({...this.state, eng: !this.state.eng})}
                name="eng"
              />
            </div>
            <button className='btn bg-mainDark-dark' onClick={this.props.logout}>
              Выйти
            </button>
          </div>
          {eng 
            ? <div>
                <div className='d-flex mx-3 mt-1 mb-3'>
                  <MainMenuLink2 url={'/reports/'} title={'Отчеты'}/>
                  <MainMenuLink2 url={'/workshop_boar/'} title={'Хрячник'}/>
                  <MainMenuLink2 url={'/veterinary/'} title={'Ветеринария'}/>
                </div>
                { activeTab.name === 'mainTab' &&
                  <ESysBlocks eData={eData} population={population} setTab={this.setTab}/>}

                { activeTab.name === 'boilerTab' &&
                  <BoilerESys getESystemsDataDetail={this.props.getESystemsDataDetail}
                    detailDataList={this.props.detailDataList} setTab={this.setTab} />}

                { activeTab.name === 'pumpsTab' &&
                  <BoilerESys 
                    getESystemsDataDetail={this.props.getESystemsDataDetail}
                    detailDataList={this.props.detailDataList} setTab={this.setTab} />}

              </div>
              : <div className="row">
                  <MainMenuLink url={'/workshop1/'} title={'Цех1 Осеменение'}/>
                  <MainMenuLink url={'/workshop2/'} title={'Цех2 Ожидание родов'}/>
                  <MainMenuLink url={'/workshop3/'} title={'Цех3 Маточник'}/>
                  <MainMenuLink url={'/workshop4/'} title={'Цех4 Доращивание'}/>
                  <MainMenuLink url={'/workshop8/'} title={'Цех8 Доращивание'}/>
                  <MainMenuLink url={'/workshop5/'} title={'Цех5 Откорм'}/>
                  <MainMenuLink url={'/workshop6/'} title={'Цех6 Откорм'}/>
                  <MainMenuLink url={'/workshop7/'} title={'Цех7 Откорм'}/>
                  <MainMenuLink url={'/workshop_boar/'} title={'Хрячник'}/>
                  <MainMenuLink url={'/veterinary/'} title={'Ветеринария'}/>
                  <MainMenuLink url={'/reports/'} title={'Отчеты'}/>
                </div>
            }
          </div>
    )
  }
}

export default MainPage