import { put, call } from 'redux-saga/effects';

import ESystemsActions from '../redux-sauce/eSystems'

export function* getESystemsData(api, action) {
    try {
        let response = yield call(api.getEngParams, action.payload);
        yield put(ESystemsActions.getESystemsDataSuccess(response));
    } catch (err) {
        yield put(ESystemsActions.getESystemsDataFail(err.message))
    }
}

export function* getESystemsDataDetail(api, action) {
    try {
        let response = yield call(api.getEngParamsLogs, action.payload);
        yield put(ESystemsActions.getESystemsDataDetailSuccess(response));
    } catch (err) {
        yield put(ESystemsActions.getESystemsDataDetailFail(err.message))
    }
}
