import axios from 'axios';
import endpoints from './endpoints';
import { parseErrorData, createUrlParamsFromFilters } from './utils';

const create = () => {
    const getEngParams = (filters) => {
        const params = createUrlParamsFromFilters(filters);
        return axios.get(endpoints.ENG_DATA, { params })
        .then(response => response.data)
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const getEngParamsLogs = (filters) => {
        const params = createUrlParamsFromFilters(filters);
        return axios.get(endpoints.ENG_DATA_LOGS, { params })
        .then(response => response.data)
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }


    return {
        getEngParams,
        getEngParamsLogs
    }
}

export default {
    create
}