import React, { Component } from 'react';

import TextField from '@material-ui/core/TextField';
import Switch from '@material-ui/core/Switch';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';


const useThStyles = makeStyles({
  root: {
    // transform: 'rotate(-90deg)', 
    verticalAlign: 'middle !important',
    padding: '0 0 0 0'
  },
});

function StyledTh(props) {
  const { title } = props
  const classes = useThStyles();
  return <Box component='th' className={classes.root}>{title}</Box>;
}

export const getDate = () => {
  let today = new Date();
  let dd = String(today.getDate()).padStart(2, '0');
  let mm = String(today.getMonth() + 1).padStart(2, '0');
  let yyyy = today.getFullYear();

  today = yyyy + '-' + mm + '-' + dd;
  let month_ago = (mm - 1) > 0 ? (mm-1).toString() : (12).toString();
  month_ago = month_ago < 10 ? '0' + month_ago : month_ago
  let month_ago_day = yyyy + '-' + month_ago + '-' + dd;
  return {start_date: month_ago_day, end_date: today}
}


function TrRow(props) {
  const { showEmpty } = props
  let transferOut = props.transferOut ? props.transferOut : 0
  let totalEndQ = (props.padejQnty ? props.padejQnty : 0) + (props.vinuzhdQnty ? props.vinuzhdQnty : 0) 
    + (props.prirezkaQnty ? props.prirezkaQnty : 0) + (props.zaboiQnty ? props.zaboiQnty : 0) + transferOut
  let totalEndW = (props.padejTotal ? props.padejTotal : 0) + (props.vinuzhdTotal ? props.vinuzhdTotal : 0) 
    + (props.prirezkaTotal ? props.prirezkaTotal : 0) + (props.transferOutW ? props.transferOutW : 0)
    + (props.zaboiTotal ? props.zaboiTotal : 0)
  let totalStartW = props.transferInW ? props.transferInW : 0

  let vinuzhdQ = (props.vinuzhdQnty > 0 && props.vinuzhdQnty) + (props.prirezkaQnty > 0 && props.prirezkaQnty)
  let vinuzhdW = (props.vinuzhdTotal > 0 && props.vinuzhdTotal) + (props.prirezkaTotal > 0 && props.prirezkaTotal)
  return (
    <tr className='text-nowrap'>
      <td>
        {/* метка */}
        {props.label}
      </td>
      <td>
        {/* наличине на начало дня гол */}
        <span className='font-500 font-14'>
          {props.startQnty}
        </span>
      </td>
      <td>
        {/* наличине на начало дня вес */}
      </td>
      <td>
        {/* переведено из других групп гол */}
        {props.transferIn && 
          <span>
            {props.transferIn}
            {/* <span className='font-11'> гол</span> */}
          </span>
        }
      </td>
      <td>
        {/* переведено из других групп вес */}
        {props.transferInW && 
          <span>
            <span>
              {props.transferInW}
              <span className='font-11'> кг</span>
            </span>
            <span className='d-block font-italic'>
              {props.transferInAvg.toFixed(2)}
              <span className='font-11'> кг</span>
            </span>
          </span> 
        }
      </td>
      {showEmpty && [
        <td>
          {/* переведено из других ферм гол */}
        </td>,
        <td>
          {/* переведено из других ферм вес */}
        </td>,
        <td>
          {/* оприходовано по результатам инвентаризации гол */}
        </td>,
        <td>
          {/* оприходовано по результатам инвентаризации вес */}
        </td>,
        <td>
          {/* переведено из других хозяйств гол */}
        </td>,
        <td>
          {/* переведено из других хозяйств вес */}
        </td>
      ]}
      <td>
        {/* привес */}
      </td>
      <td>
        {/* итого прихода гол */}
        {props.transferIn &&
          <span className='font-500'>
            {props.transferIn}
            {/* <span className='font-11'> гол</span> */}
          </span>
        }
      </td>
      <td>
        {/* итого прихода вес */}
        {totalStartW > 0 && <span>{totalStartW}<span className='font-11'> кг</span></span>}
      </td>
      {showEmpty && [
        <td>
          {/* продано государству гол */}
        </td>,
        <td>
          {/* продано государству вес */}
        </td>
      ]}
      <td>
        {/* переведено в другие группы гол */}
        {props.transferOut}
      </td>
      <td>
        {/* переведено в другие группы вес */}
        {props.transferOutW && <span>{props.transferOutW}<span className='font-11'> кг</span></span>}
        {props.transferOutAvg && 
          <span className='d-block font-italic'>
            {props.transferOutAvg.toFixed(2)}
            <span className='font-11'> кг</span>
          </span>}
      </td>
      {showEmpty && [
        <td>
          {/* отнято по результатам инвентаризации гол */}
        </td>,
        <td>
          {/* отнято по результатам инвентаризации вес */}
        </td>
      ]}
      <td>
        {/* забито гол */}
        {props.zaboiQnty > 0 && props.zaboiQnty}
      </td>
      <td>
        {/* забито вес */}
        {props.zaboiTotal > 0 && <span>{props.zaboiTotal}<span className='font-11'> кг</span></span>}
        {props.zaboiAvg && 
          <span className='d-block font-italic'>
            {props.zaboiAvg.toFixed(2)}
            <span className='font-11'> кг</span>
          </span>}
      </td>
      <td>
        {/* пало гол */}
        {props.padejQnty > 0 && props.padejQnty}
      </td>
      <td>
        {/* пало вес */}
        {props.padejTotal > 0 && <span>{props.padejTotal}<span className='font-11'> кг</span></span>}
      </td>
      <td>
        {/* вынужденный забой гол */}
        {vinuzhdQ > 0 && vinuzhdQ}
      </td>
      <td>
        {/* вынужденный забой вес */}
        {vinuzhdW > 0 && <span>{vinuzhdW}<span className='font-11'> кг</span></span>}
      </td>
      <td>
        {/* итого расхода гол */}
        
        <span className='font-500'>
          {totalEndQ > 0 && totalEndQ}</span>
      </td>
      <td>
        {/* итого расхода вес */}
          {totalEndW > 0 && <span>{totalEndW}<span className='font-11'> кг</span></span>}
      </td>
      <td>
        {/* наличие на конец месяца гол */}
        <span className='font-500 font-14'>
          {props.endQnty}</span>
      </td>
      <td>
        {/* наличие на конец месяца вес */}
      </td>
      <td>
        {/* кормодни */}
      </td>
    </tr>
  )
}

class Report24fComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      startDate: null,
      endDate: null,
      showEmpty: false,

    }
    this.setData = this.setData.bind(this);
    this.genRep = this.genRep.bind(this);
	}

  componentDidMount() {
    this.props.getF24Report(getDate())
    const { start_date, end_date } = getDate()
    this.setState({
      ...this.state,
      startDate: start_date,
      endDate: end_date,
    })
  }

  setData (e) {
    this.setState({
      ...this.state,
      [e.target.name]: e.target.value
    })
  }

  genRep() {
    this.props.getF24Report(
      {start_date: this.state.startDate, end_date: this.state.endDate}
    )
  }

  render() {
    const { report24f } = this.props
    const { showEmpty } = this.state
    return (
      <div className="card">
        <div className='content'>
          <h3 className='mt-1 mb-3'>Отчет 24f</h3>
          <div className="mt-2 mb-3 mx-2">
            <TextField
                label={'Дата с'}
                type="date"
                name='startDate'
                className='mr-2'
                value={this.state.startDate}
                onChange={this.setData}
                InputLabelProps={{
                  className: '',
                  shrink: true 
                }}
              />
              <TextField
                label={'Дата до'}
                type="date"
                name='endDate'
                className='mr-2'
                value={this.state.endDate}
                onChange={this.setData}
                InputLabelProps={{
                  className: '',
                  shrink: true 
                }}
              />
              <button className='btn bg-mainDark-dark mr-2 ' onClick={this.genRep}>
                Сформировать
              </button>
              <span className='ml-2'>
                <span>Полный вид</span>
                <Switch
                  checked={this.state.showEmpty}
                  onChange={() => this.setState({...this.state, showEmpty: !this.state.showEmpty})}
                  name="showEmpty"
                />  
              </span>
          </div>
        </div>
        <div className='content'>
          <table className='table table-responsive table-sm'>
            <thead className='font-12' 
              style={{lineHeight: '15px'}}>
              <StyledTh title={'Группа животных'} />
              <StyledTh title={'начало месяца гол'} />
              <StyledTh title={'начало месяца вес'} />
              <StyledTh title={'перевод из других групп гол'} />
              <StyledTh title={'перевод из других групп вес'} />
              {showEmpty && [
                <StyledTh title={'перевод из других ферм гол'} />,
                <StyledTh title={'перевод из других ферм вес'} />,
                <StyledTh title={'оприходовано по результатам инвентаризации гол'} />,
                <StyledTh title={'оприходовано по результатам инвентаризации вес'} />,
                <StyledTh title={'перевод из других хозяйств гол'} />,
                <StyledTh title={'перевод из других хозяйств вес'} />,
              ]}
              <StyledTh title={'привес'} />
              <StyledTh title={'итого прихода гол'} />
              <StyledTh title={'итого прихода вес'} />
              {showEmpty && [
                <StyledTh title={'продано государству гол'} />,
                <StyledTh title={'продано государству вес'} />
              ]}
              <StyledTh title={'перевод в другие группы гол'} />
              <StyledTh title={'перевод в другие группы вес'} />
              {showEmpty && [
                <StyledTh title={'отнято по результатам инвентаризации гол'} />,
                <StyledTh title={'отнято по результатам инвентаризации вес'} />
              ]}
              <StyledTh title={'забито гол'} />
              <StyledTh title={'забито вес'} />
              <StyledTh title={'пало гол'} />
              <StyledTh title={'пало вес'} />
              <StyledTh title={'вынужд убой + прирез гол'} />
              <StyledTh title={'вынужд убой + прирез вес'} />
              <StyledTh title={'итого расхода гол'} />
              <StyledTh title={'итого расхода вес'} />
              <StyledTh title={'наличие на конец месяца'} />
              <StyledTh title={'кормодни'} />
            </thead>
            {report24f && 
              <tbody>
                {/* хряки */}
                <TrRow 
                  showEmpty={showEmpty}
                  label={'ХР/ПР'}
                  startQnty={report24f['start_boars_count']}
                  padejQnty={report24f['boars_culls'] && report24f['boars_culls']['padej_qnty_osn']}
                  padejTotal={report24f['boars_culls'] && report24f['boars_culls']['padej_total_osn']}
                  vinuzhdQnty={report24f['boars_culls'] && report24f['boars_culls']['vinuzhd_qnty_osn']}
                  vinuzhdTotal={report24f['boars_culls'] && report24f['boars_culls']['vinuzhd_total_osn']}
                  endQnty={report24f['end_boars_count']}
                />
                {/* основные свиноматки */}
                <TrRow 
                  showEmpty={showEmpty}
                  label={'0/М'}
                  startQnty={report24f['start_sows_osn_count']}
                  transferIn={report24f['sow_group_transfer'] && report24f['sow_group_transfer']['count_prov_to_osn']}
                  padejQnty={report24f['culls_sows'] && report24f['culls_sows']['padej_qnty_osn']}
                  padejTotal={report24f['culls_sows'] && report24f['culls_sows']['padej_total_osn']}
                  vinuzhdQnty={report24f['culls_sows'] && report24f['culls_sows']['vinuzhd_qnty_osn']}
                  vinuzhdTotal={report24f['culls_sows'] && report24f['culls_sows']['vinuzhd_total_osn']}
                  endQnty={report24f['end_sows_osn_count']}
                />
                {/* проверяемые свиноматки */}
                <TrRow 
                  showEmpty={showEmpty}
                  label={'Пров/См'}
                  startQnty={report24f['start_sows_prov_count']}
                  transferIn={report24f['sow_group_transfer'] && report24f['sow_group_transfer']['count_rem_to_prov']}
                  transferOut={report24f['sow_group_transfer'] && report24f['sow_group_transfer']['count_prov_to_osn']}
                  padejQnty={report24f['culls_sows'] && report24f['culls_sows']['padej_qnty_prov']}
                  padejTotal={report24f['culls_sows'] && report24f['culls_sows']['padej_total_prov']}
                  vinuzhdQnty={report24f['culls_sows'] && report24f['culls_sows']['vinuzhd_qnty_prov']}
                  vinuzhdTotal={report24f['culls_sows'] && report24f['culls_sows']['vinuzhd_total_prov']}
                  endQnty={report24f['end_sows_prov_count']}
                />
                {/* ремонтные хряки */}
                <TrRow label={'Р/ХР'} showEmpty={showEmpty}/>
                {/* ремонтные свиноматки */}
                <TrRow 
                  showEmpty={showEmpty}
                  label={'Р/СВ'}
                  startQnty={report24f['start_sows_rem_count']}
                  transferIn={report24f['sow_group_transfer'] && report24f['sow_group_transfer']['to_rem']}
                  transferOut={report24f['sow_group_transfer'] && report24f['sow_group_transfer']['count_rem_to_prov']}
                  padejQnty={report24f['culls_sows'] && report24f['culls_sows']['padej_qnty_rem']}
                  padejTotal={report24f['culls_sows'] && report24f['culls_sows']['padej_total_rem']}
                  vinuzhdQnty={report24f['culls_sows'] && report24f['culls_sows']['vinuzhd_qnty_rem']}
                  vinuzhdTotal={report24f['culls_sows'] && report24f['culls_sows']['vinuzhd_total_rem']}
                  endQnty={report24f['end_sows_rem_count']}
                />
                {/* поросята сосуны */}
                <TrRow 
                  showEmpty={showEmpty}
                  label={'П/С'}
                  startQnty={report24f['start_ws3_piglets_count']}
                  transferIn={report24f['total_born_alive'] && report24f['total_born_alive']}
                  transferOut={report24f['count_doros_otkorm_in_out'] 
                    && report24f['count_doros_otkorm_in_out']['qnty_to_doros']}
                  transferOutW={report24f['count_doros_otkorm_in_out'] 
                    && report24f['count_doros_otkorm_in_out']['total_to_doros']}
                  transferOutAvg={report24f['count_doros_otkorm_in_out'] 
                    && report24f['count_doros_otkorm_in_out']['avg_to_doros']}
                  padejQnty={report24f['ws3_culls'] && report24f['ws3_culls']['padej_qnty_ws3']}
                  padejTotal={report24f['ws3_culls'] && report24f['ws3_culls']['padej_total_ws3']}
                  vinuzhdQnty={report24f['ws3_culls'] && report24f['ws3_culls']['vinuzhd_qnty_ws3']}
                  vinuzhdTotal={report24f['ws3_culls'] && report24f['ws3_culls']['vinuzhd_total_ws3']}
                  prirezkaQnty={report24f['ws3_culls'] && report24f['ws3_culls']['prirezka_qnty_ws3']}
                  prirezkaTotal={report24f['ws3_culls'] && report24f['ws3_culls']['prirezka_total_ws3']}
                  endQnty={report24f['end_ws3_piglets_count']}
                />
                {/* поросята дорос */}
                <TrRow 
                  showEmpty={showEmpty}
                  label={'с 2 по 8'}
                  startQnty={report24f['start_ws48_piglets_count']}
                  transferIn={report24f['count_doros_otkorm_in_out'] 
                    && report24f['count_doros_otkorm_in_out']['qnty_to_doros']}
                  transferInW={report24f['count_doros_otkorm_in_out'] 
                    && report24f['count_doros_otkorm_in_out']['total_to_doros']}
                  transferInAvg={report24f['count_doros_otkorm_in_out'] 
                    && report24f['count_doros_otkorm_in_out']['avg_to_doros']}
                  transferOut={report24f['count_doros_otkorm_in_out'] 
                    && report24f['count_doros_otkorm_in_out']['qnty_to_otkorm']}
                  transferOutW={report24f['count_doros_otkorm_in_out'] 
                    && report24f['count_doros_otkorm_in_out']['total_to_otkorm']}
                  transferOutAvg={report24f['count_doros_otkorm_in_out'] 
                    && report24f['count_doros_otkorm_in_out']['avg_to_otkorm']}
                  padejQnty={report24f['ws48_culls'] && report24f['ws48_culls']['padej_qnty_ws48']}
                  padejTotal={report24f['ws48_culls'] && report24f['ws48_culls']['padej_total_ws48']}
                  vinuzhdQnty={report24f['ws48_culls'] && report24f['ws48_culls']['vinuzhd_qnty_ws48']}
                  vinuzhdTotal={report24f['ws48_culls'] && report24f['ws48_culls']['vinuzhd_total_ws48']}
                  prirezkaQnty={report24f['ws48_culls'] && report24f['ws48_culls']['prirezka_qnty_ws48']}
                  prirezkaTotal={report24f['ws48_culls'] && report24f['ws48_culls']['prirezka_total_ws48']}
                  endQnty={report24f['end_ws48_piglets_count']}
                />
                {/* поросята откорм */}
                <TrRow 
                  showEmpty={showEmpty}
                  label={'с 4'}
                  startQnty={report24f['start_ws567_piglets_count']}
                  transferIn={report24f['count_doros_otkorm_in_out'] 
                    && report24f['count_doros_otkorm_in_out']['qnty_to_otkorm']}
                  transferInW={report24f['count_doros_otkorm_in_out'] 
                    && report24f['count_doros_otkorm_in_out']['total_to_otkorm']}
                  transferInAvg={report24f['count_doros_otkorm_in_out'] 
                    && report24f['count_doros_otkorm_in_out']['avg_to_otkorm']}
                  transferOut={report24f['sow_group_transfer'] 
                    && report24f['sow_group_transfer']['to_rem']}
                  transferOutW={report24f['count_doros_otkorm_in_out'] 
                    && report24f['count_doros_otkorm_in_out']['total_to_rem']}
                  zaboiAvg={report24f['ws567_culls'] && report24f['ws567_culls']['spec_avg_ws567']}
                  zaboiQnty={report24f['ws567_culls'] && report24f['ws567_culls']['spec_qnty_ws567']}
                  zaboiTotal={report24f['ws567_culls'] && report24f['ws567_culls']['spec_total_ws567']}
                  padejQnty={report24f['ws567_culls'] && report24f['ws567_culls']['padej_qnty_ws567']}
                  padejTotal={report24f['ws567_culls'] && report24f['ws567_culls']['padej_total_ws567']}
                  vinuzhdQnty={report24f['ws567_culls'] && report24f['ws567_culls']['vinuzhd_qnty_ws567']}
                  vinuzhdTotal={report24f['ws567_culls'] && report24f['ws567_culls']['vinuzhd_total_ws567']}
                  endQnty={report24f['end_ws567_piglets_count']}
                />
              </tbody>
            }
          </table>
        </div>
      </div>
    );
  }
}


export default Report24fComponent