import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import { toggleArray } from '../utils';

let measureClass = 'font-italic font-11'

function NorecTimeout(param, measure) {
  if (param === 'norecord') return <span className='color-red1-light'>н/д</span>
  if (param === 'timedout') return <span className='color-red1-light font-10'>не обновляется</span>
  if (parseFloat(param)) {
    if (measure === 'м3/ч')
      return [param.toFixed(3), <span className={measureClass + ' pl-1'}>{measure}</span>]
    return [param.toFixed(1), <span className={measureClass + ' pl-1'}>{measure}</span>]
  }
  if (!param) return <span className='color-red1-light'>н/д</span>

  return param
}

function EngParams (props) {
  let temp  = NorecTimeout(props.temp, 'C')
  let hum  = NorecTimeout(props.hum, '%')

  return (
    <div className='d-flex justify-content-between'>
      <p className='mb-0 mr-2'>
        <i className="fas fa-thermometer-quarter color-red1-light mr-1"></i>
        {temp}
      </p>
      {/* <p className='mb-0 mr-2'>
        <i className="fas fa-tint color-blue1-light mr-1"></i>{hum} 
      </p> */}
    </div>
  )
}

function Boiler (props) {
  const { eData } = props
  let stemp1, stemp2, temp, rtemp, water
  if (eData) {
    stemp1 = eData['stemp1'] && NorecTimeout(eData['stemp1'], 'C')
    stemp2 = eData['stemp2'] && NorecTimeout(eData['stemp2'], 'C')
    temp = eData['temp'] && NorecTimeout(eData['temp'], 'C')
    rtemp = eData['rtemp'] && NorecTimeout(eData['rtemp'], 'C')
    water = eData['water'] && NorecTimeout(eData['water'], 'м3/ч')
  }
  return(
    <div>
      <tr>
        <td ><span className='mr-2'>Потребление</span></td>
        <td><i className="fas fa-tint mr-1"></i>
        {water}</td>
      </tr>
      <tr>
        <td ><span className='mr-2'>Подача 1</span></td>
        <td>
          <i className="fas fa-thermometer-quarter color-red1-light mr-1"></i>
          {stemp1}
        </td>
      </tr>
      <tr>
        <td ><span className='mr-2'>Подача 2</span></td>
        <td>
          <i className="fas fa-thermometer-quarter color-red1-light mr-1"></i>
          {stemp2}
        </td>
      </tr>
      <tr>
        <td ><span className='mr-2'>Смешанная</span></td>
        <td>
          <i className="fas fa-thermometer-quarter color-red1-light mr-1"></i>
          {temp}
        </td>
      </tr>
      <tr>
        <td ><span className='mr-2'>Отбратка</span></td>
        <td>
          <i className="fas fa-thermometer-quarter color-red1-light mr-1"></i>
          {rtemp}
        </td>
      </tr>
    </div>
  )
}

function Pump (props) {
  const { eData } = props
  let insidetemp, outsidetemp, pressure, water, wellwater, ecofoodwater
  if (eData) {
    insidetemp = eData['insidetemp'] && NorecTimeout(eData['insidetemp'], 'C')
    outsidetemp = eData['outsidetemp'] && NorecTimeout(eData['outsidetemp'], 'C')
    pressure = eData['pressure'] && NorecTimeout(eData['pressure'], 'Бар')
    water = eData['water'] && NorecTimeout(eData['water'], 'м3/6ч')
    wellwater = eData['wellwater'] && NorecTimeout(eData['wellwater'], 'м3/6ч')
    ecofoodwater = eData['ecofoodwater'] && NorecTimeout(eData['ecofoodwater'], 'м3/6ч')
  }
  return(
    <div>
      <tr>
        <td ><span className='mr-2'>Температура на улице</span></td>
        <td>
          <i className="fas fa-thermometer-quarter color-blue1-light mr-1"></i>
          {outsidetemp}
        </td>
      </tr>
      <tr>
        <td ><span className='mr-2'>Температура внутри</span></td>
        <td><i className="fas fa-thermometer-quarter color-red1-light mr-1"></i>
        {insidetemp}</td>
      </tr>
      <tr>
        <td ><span className='mr-2'>Давление</span></td>
        <td>
          <i className="fas fa-weight color-black-light mr-1"></i>
          {pressure}
        </td>
      </tr>
      <tr>
        <td ><span className='mr-2'>Вода</span></td>
        <td>
          <i className="fas fa-tint color-blue1-light mr-1"></i>
          {water}
        </td>
      </tr>
      <tr>
        <td ><span className='mr-2'>wellwater</span></td>
        <td>
          <i className="fas fa-tint color-blue2-light mr-1"></i>
          {wellwater}
        </td>
      </tr>
      <tr>
        <td ><span className='mr-2'>Расход Экофуд</span></td>
        <td>
          <i className="fas fa-tint  color-green1-light mr-1"></i>
          {ecofoodwater}
        </td>
      </tr>
    </div>
  )
}

function WS12Sows (props) {
  const { sows } = props
  const rowClass = ' font-12 mb-0 d-flex justify-content-between'
  return (
    sows  ?
      <div className='mb-2'>
        <p className='mb-0 font-600'>Свиноматок {sows.sows_count}</p>
        <div className='d-flex justify-content-between' style={{lineHeight: '18px'}}>
          <div className=''>
            <p className={rowClass}>
              <span className='mr-2'>ожидают осемения</span>
              <span>{sows.sows_wait_count}</span>
            </p>
            <p className={rowClass}>
              <span className='mr-2'>осеменены</span>
              <span>{sows.sows_sem_count}</span>
            </p>
            <p className={rowClass}>
              <span className='mr-2'>супоросные</span>
              <span>{sows.sows_sup_count}</span>
            </p>
            <p className={rowClass}>
              <span className='mr-2'>ремонтные</span>
              <span>{sows.sows_rem_count}</span>
            </p>
          </div>
        </div>
      </div>
      : ''
  )
}

function WS3ows (props) {
  const { sows } = props
  const rowClass = ' font-12 mb-0 d-flex justify-content-start'
  return (
    sows ?
      <div className='mb-2'>
        <p className='mb-0 font-600'>Свиноматок {sows.sows_count}</p>
        <div className='' style={{lineHeight: '18px'}}>
          <p className={rowClass}>
            <span className='mr-2'>супоросные</span>
            <span>{sows.sows_sup_count}</span>
          </p>
          <p className={rowClass}>
            <span className='mr-2'>подсосные</span>
            <span>{sows.sows_far_count}</span>
          </p>
          <p className={rowClass}>
            <span className='mr-2'>кормилицы</span>
            <span>{sows.sows_nurse_count}</span>
          </p>
        </div>
      </div>
    : ''
  )
}

function Workshop12 (props) {
  const { eData, title, url, populationData } = props
  let temp1, humi1, electro1, water, temp2, humi2, electro2

  if (eData) {
    temp1 = eData['temp1'] && eData['temp1']
    humi1 = eData['humi1'] && eData['humi1']
    electro1 = eData['electro'] && eData['electro']
    electro1 = NorecTimeout(electro1, 'КВт/ч')
    water = eData['water'] && eData['water']
    water = NorecTimeout(water, 'м3/ч')
    
    temp2 = eData['temp2'] && eData['temp2']
    humi2 = eData['humi2'] && eData['humi2']

  } else {
    temp1 = humi1 = electro1 = water = temp2 = humi2 = electro2 = 'н/д'
  }

  return (
    <div className='card card-style' style={{'min-width': '300px'}}>
      <div className='content'>
        <div className='d-flex justify-content-between'>
          <h5>{title}</h5>
          <Link to={url}>Операции</Link>
        </div>
        {eData && 
          <div className='d-flex justify-content-between'>
            <EngParams temp={temp1} hum={humi1} water={water}/>
            <EngParams temp={temp2} hum={humi2} water={water}/>
          </div>
        }
        <div className='d-flex justify-content-between'>
          <p className='mb-0 mr-2'>
            <i className="fas fa-bolt color-yellow1-light mr-1"></i>
            {electro1} 
          </p>
          <p className='mb-0 mr-2'> 
            <i className="fas fa-tint mr-1"></i>
            {water} 
          </p>
        </div>
        <div>
          <WS12Sows sows={populationData}/>
        </div>
      </div>
    </div>
  )
}


function WsAgePopulation (props) {
  const { pigletsByAge } = props
  const ages = (label) => {
    return `
      ${label.split('_')[2]} 
      ${label.split('_')[3] !== 'plus' ? '-' : ''}
      ${label.split('_')[3] === 'plus' ? '+' : label.split('_')[3]}`
  }

  return (
    pigletsByAge ?
      <div className='mx-1' style={{lineHeight: '18px'}}>
        <p className='mb-0 color-mainGrey-dark font-500 mb-1'>По возрасту</p>
        {Object.entries(pigletsByAge).map(item => item[1] && item[0].includes('count_p') &&
          <div className='mb-0 d-flex justify-content-between flex-wrap'>
            <span className='mr-2'>{ages(item[0])}
            </span>
            <span className='mr-1'>{item[1]} <span className={measureClass}>гол</span></span>
          </div>
          )}
      </div>
    : ''
  )
}

function WsTours (props) {
  const { tours } = props

  return (
    tours && tours.length > 0 ?
      <div className='mx-1' style={{lineHeight: '18px'}}>
        <p className='mb-0 color-mainGrey-dark font-500 mb-1' >По турам</p>
          {tours.map(tour =>
            <div className='mb-0 d-flex justify-content-between flex-wrap'>
              <span className='mr-2'>{tour.week_number} <span className={measureClass}> нед</span> </span>
              <span className='mr-2'>{tour.year}</span>
              <span className='mr-2'>{tour.qnty}<span className={measureClass}> гол</span></span>
            </div>
            )}
      </div>
    : ''
  )
}


function EngSectionTr (props) {
  let temp = NorecTimeout(props.section.temp, 'C')
  let humi = NorecTimeout(props.section.humi, '%')
  // let elec = NorecTimeout(props.section.elec, 'КВт/ч')
  let secWater = NorecTimeout(props.section.water, 'м3/ч')
  return (
    <tr>
      <td >{props.section.number}</td>
      <td>{temp}</td>
      {/* <td>{humi}</td> */}
      {/* <td>{elec}</td> */}
      {props.water &&
        <td>{secWater}</td>
      }
    </tr>
  )
}


function EngTable (props) {
  const { sections, water, } = props
  
  return (
    <table className='table table-sm w-100 color-mainGrey-dark' style={{lineHeight: '18px'}}>
      <thead>
        <th>
          №
        </th>
        <th>
          <i className="fas fa-thermometer-quarter color-red1-light mr-1"></i>
        </th>
        {/* <th>
          <i className="fas fa-tint color-blue1-light mr-1"></i>
        </th> */}
        {/* <th>
          <i className="fas fa-bolt color-yellow1-light mr-1"></i>
        </th> */}
        {water &&
          <th>
            <i className="fas fa-tint mr-1"></i>
          </th>
        }
      </thead>
      <tbody>
        {sections.map(section => section.number && 
          <EngSectionTr section={section} water={water}/>
        )}
      </tbody>
    </table>
  )
}

function WorkshopWithSections(props) {
  const { url, title, eData, populationData, water } = props
  let sections = []
  let pigletsByAge = {}
  let pigletsByTours = {}
  let electro = null
  if (eData) electro = NorecTimeout(eData['electro'], 'КВт/ч')

  populationData && Object.keys(populationData).forEach(function(key) {
    if (key.includes('count_piglets')) pigletsByAge[key] = populationData[key]
  })

  if (populationData) pigletsByTours = populationData['piglets_by_tours']

  eData && Object.keys(eData).forEach(function(key,i) {
    let sec = {
      number: key.split('sect')[1],
      temp: eData[key]['temp'],
      humi: eData[key]['humi'],
      water: eData[key]['water']
    }
    sections = toggleArray(sections, sec)
  })

  return (
    <div className='card card-style color-mainGrey-dark align-self-baseline' style={{'min-width': '300px'}}>
      <div className='content'>
        <div className='d-flex justify-content-between'>
          <h5>{title}</h5>
          <Link to={url}>Операции</Link>
        </div>
        <p className='mb-1 mr-2'>
          <i className="fas fa-bolt color-yellow1-light mr-1"></i>
          {electro} 
        </p>
        <EngTable sections={sections} water={water}/>
        <p className='mb-0 font-600 color-mainGrey-dark'>Поголовье <span className='font-14'>
          {populationData && populationData['pigs_count']}</span></p>
        <div className='d-flex justify-content-between'>
          <WsTours tours={pigletsByTours} />
          <WsAgePopulation pigletsByAge={pigletsByAge}/>
        </div>
      </div>
    </div>
  )
}

function Workshop3WithSections(props) {
  const { url, title, eData, populationData } = props
  let sections = []
  let sows = {}
  let pigletsByAge = {}
  let pigletsByTours = {}
  let electro = null
  if (eData) electro = NorecTimeout(eData['electro'], 'КВт/ч')
  let water = null
  if (eData) water = NorecTimeout(eData['water'], 'м3/ч')

  populationData && Object.keys(populationData).forEach(function(key) {
    if (key.includes('sow')) sows[key] = populationData[key]
    if (key.includes('count_piglets')) pigletsByAge[key] = populationData[key]
  })

  if (populationData) pigletsByTours = populationData['piglets_by_tours']
    
  eData && Object.keys(eData).forEach(function(key,i) {
    if (eData[key]) {
      let sec = {
        number: key.split('sect')[1],
        temp: eData[key]['temp'],
        humi: eData[key]['humi']
      }
      sections = toggleArray(sections, sec)
  }
  })

  return (
    <div className='card card-style color-mainGrey-dark align-self-baseline' style={{'min-width': '300px'}}>
      <div className='content'>
        <div className='d-flex justify-content-between'>
          <h5>{title}</h5>
          <Link to={url}>Операции</Link>
        </div>
        <div className='d-flex justify-content-between'>
          <p className='mb-1 mr-2'>
            <i className="fas fa-bolt color-yellow1-light mr-1"></i>
            {electro} 
          </p>
          <p className='mb-0 mr-2'> 
            <i className="fas fa-tint mr-1"></i>
            {water} 
          </p>
        </div>
        <EngTable sections={sections} />
        <WS3ows sows={sows}/>
        <p className='mb-0 font-600 color-mainGrey-dark'>поросята <span className='font-14'>
            {populationData && populationData['pigs_count']}</span></p>
        <div className='d-flex justify-content-between'>
          <WsTours tours={pigletsByTours}/>
          <WsAgePopulation pigletsByAge={pigletsByAge}/>
        </div>
      </div>
    </div>
  )
}

function genSec(number){
  let sections = []
  for (let i = 1; i <= number; i++) {
    sections = toggleArray(sections, 
      {number: i, temp: 26, hum: 55, elec: 45, water: 123})
  }
  return sections
}


export function ESysBlocks (props) {
  const { eData, population, setTab } = props
  return (
    <div className='d-flex justify-content-start flex-wrap'>
      <div>
        <div className='d-flex justify-content-start flex-wrap'>
          <Workshop12 url={'/workshop1/'} title={'Цех1 '}
            eData={eData['ws1']}
            populationData={population['ws12'] && population['ws12'][0]}
            />
          <Workshop12 url={'/workshop2/'} title={'Цех2 '}
            eData={eData['ws2']}
            populationData={population['ws12'] && population['ws12'][1]} 
            />
        </div>
        <div className='d-flex justify-content-start flex-wrap'>
          <Workshop3WithSections url={'/workshop3/'} title={'Цех3 Маточник'}
            sections={genSec(6)} eData={eData['ws3']}
            populationData={population['ws3']}
            />
          <WorkshopWithSections url={'/workshop4/'} title={'Цех4 Доращивание'}
            sections={genSec(10)} eData={eData['ws4']}
            populationData={population['ws4']} water={false}
            />
          <WorkshopWithSections url={'/workshop8/'} title={'Цех8 Доращивание'}
            populationData={population['ws8']}
            sections={genSec(4)} eData={eData['ws8']} water={false}/>
        </div>
        <div className='d-flex justify-content-start flex-wrap'>
          <WorkshopWithSections url={'/workshop5/'} title={'Цех5 Откорм'} eData={eData['ws5']}
            sections={genSec(4)}
            populationData={population['ws567'] && population['ws567'][0]} water={true}
            />
          <WorkshopWithSections url={'/workshop6/'} title={'Цех6 Откорм'} eData={eData['ws6']}
            populationData={population['ws567'] && population['ws567'][1]} water={true}
            sections={genSec(4)}/>
          <WorkshopWithSections url={'/workshop7/'} title={'Цех7 Откорм'} eData={eData['ws7']}
            populationData={population['ws567'] && population['ws567'][2]} water={true}
            sections={genSec(4)}/>
        </div>
      </div>
      <div className=''>
        <div className='card card-style'>
          <div className='content'>
            <h4 onClick={() => setTab({name: 'boilerTab'})}>Котельная</h4>
            <Boiler eData={eData['boiler']}/>
          </div>
        </div>
        <div className='card card-style'>
          <div className='content'>
            <h4>Насосная станция</h4>
            <Pump eData={eData['pumps']}/>
          </div>
        </div>
      </div>
    </div>
  )
}