import { createReducer, createActions } from 'reduxsauce'
import Immutable from 'seamless-immutable'
import {} from '../../components/utils'
/* ------------- Types and Action Creators ------------- */

const { Types, Creators } = createActions({
    getESystemsDataRequest: ['payload'],
    getESystemsDataFail: ['error'],
    getESystemsDataSuccess: ['payload'],

    getESystemsDataDetailRequest: ['payload'],
    getESystemsDataDetailFail: ['error'],
    getESystemsDataDetailSuccess: ['payload'],

})

export const ESystemsTypes = Types
export default Creators

/* ------------- Initial State ------------- */

export const INITIAL_STATE = Immutable({
    fetching: false,
    params: {},
    detailDataList: [], 
    error: null,
    message: ''
})

/* ------------- Selectors ------------- */

export const ESystemsSelectors = {
    // getESystemsData: state => state.tourReportslist,
}

/* ------------- Reducers ------------- */
//
export const getESystemsDataRequest = (state, { payload }) => {
    return state.merge({ reportsFetching: true, params: {}})
}

export const getESystemsDataSuccess = (state, { payload }) => {
    return state.merge({ reportsFetching: false, reportsError: null, params: payload })
}

export const getESystemsDataFail = (state, { error }) => {
    return state.merge({ reportsFetching: false, reportsError: error, params: {} })
}

export const getESystemsDataDetailRequest = (state, { payload }) => {
    return state.merge({ reportsFetching: true, params: {}})
}

export const getESystemsDataDetailSuccess = (state, { payload }) => {
    return state.merge({ reportsFetching: false, reportsError: null, detailDataList: payload })
}

export const getESystemsDataDetailFail = (state, { error }) => {
    return state.merge({ reportsFetching: false, reportsError: error, detailDataList: [] })
}
/* ------------- Hookup Reducers To Types ------------- */

export const reducer = createReducer(INITIAL_STATE, {
    [Types.GET_E_SYSTEMS_DATA_REQUEST]: getESystemsDataRequest,
    [Types.GET_E_SYSTEMS_DATA_SUCCESS]: getESystemsDataSuccess,
    [Types.GET_E_SYSTEMS_DATA_FAIL]: getESystemsDataFail,

    [Types.GET_E_SYSTEMS_DATA_DETAIL_REQUEST]: getESystemsDataDetailRequest,
    [Types.GET_E_SYSTEMS_DATA_DETAIL_SUCCESS]: getESystemsDataDetailSuccess,
    [Types.GET_E_SYSTEMS_DATA_DETAIL_FAIL]: getESystemsDataDetailFail,
})
