import React, { Component } from 'react';

//components
import { CreateMassVetEventForm } from './VetForms';
import { PigletsCells, Sections } from '../Locations';
import {  PigletsVetCellDetail } from '../PigletsRepresentations';
import { FetchingErrorComponentMessage, ErrorOrMessage } from '../CommonComponents';
import { getToday, toggleArrayDictById, toggleArray, dateToDateTime } from '../utils';


class WSPigletsVetTab extends Component {
   constructor(props) {
    super(props);
    this.state = {
      activeSectionId: null,

      activeLocationsIds: [],
      activeLocations: [],

      date: null,
      expand: false,
      needToRefresh: false,
    }
    this.clickSection = this.clickSection.bind(this);
    this.clickLocation = this.clickLocation.bind(this);
    this.createMassVetEventPiglets = this.createMassVetEventPiglets.bind(this);
    this.filterType = this.filterType.bind(this);
  }

  componentDidMount() {
    this.props.getRecipes()
  }
  
  clickSection (e) {
    const { sectionId } = e.target.dataset
    this.setState({
      ...this.state,
      activeSectionId: sectionId
    })
    this.props.getLocations({by_section: sectionId, cells_piglets_vet: true})
  }

  clickLocation (location) {
    let { expand, activeLocationsIds, activeLocations } = this.state
    if (!expand && !location.is_piglets_empty) expand = true

    if (!location.is_piglets_empty) {
      activeLocationsIds = toggleArray(activeLocationsIds, location.id)
      activeLocations = toggleArrayDictById(activeLocations, location)
    }
    this.setState({
      ...this.state,
      expand: expand,
      activeLocationsIds: activeLocationsIds,
      activeLocations: activeLocations,
    })    
    this.props.pigletsResetErrorsAndMessages()
  }

  createMassVetEventPiglets () {
    const { activeLocations } = this.state
    let data = {date: null, med_type: null, recipe: null, piglets_list: []}
    let piglets_list = []

    data['date'] = dateToDateTime(this.props.form.values.date)
    data['med_type'] = this.props.form.values.med_type
    data['recipe'] = this.props.form.values.recipe

    activeLocations.map(location => 
      piglets_list = toggleArray(piglets_list, parseInt(location.piglets[0].id))
      )
    data['piglets_list'] = piglets_list
    this.props.createMassVetEventPiglets(data)
    this.setState({
      ...this.state,

      expand: false,
      needToRefresh: true, 
      activeLocationsIds: [],
      activeLocations: []
    })
  }

  filterType (e) {
    this.props.getRecipes({med_type: e.target.value})
  }

  refreshSowsList () {
    if (!this.props.eventFetching && this.state.needToRefresh){
      setTimeout(() => {
        this.setState({...this.state, needToRefresh: false})
        this.props.getLocations({by_section: this.state.activeSectionId, cells_piglets_vet: true})
      }, 300)
    }
  }

  render() {
    this.refreshSowsList()
    const { sections, locations, recipes, eventFetching, eventError, message, grid,
       workshopNumber } = this.props
    const { activeLocations, expand, activeLocationsIds } = this.state

    let today = getToday()

    return (
        <div className='pb-5 mb-5'>
          <FetchingErrorComponentMessage 
            fetching={this.props.sectionsFetching}
            error={this.props.sectionsListError}
            message={null}
            component={
              <Sections 
                sections={sections}
                activeSectionId={this.state.activeSectionId}
                clickSection={this.clickSection}
              />}
            />
          <FetchingErrorComponentMessage 
            fetching={this.props.locationsFetching}
            error={this.props.locationsErrorList}
            message={null}
            component={
              <PigletsCells
                isSection={this.state.activeSectionId}
                fetching={this.props.locationsFetching}
                locations={locations}
                activeCellIds={activeLocationsIds}
                clickLocation={this.clickLocation}
                user={this.props.user}
                grid={grid}
                className='mb-5 pb-5'
                isVetCell={true}
              />}
          />
          <div className='card card-style fixed-bottom mx-1 my-1'>
            <div className='content'>
              <div className='' 
                onClick={() => this.setState({...this.state, expand: !this.state.expand})}>
                {expand 
                  ? <p className='my-0 text-center'>
                      <i className="fas fa-chevron-down "><span className='ml-1'>Скрыть</span></i> 
                    </p>
                  : [<p className='my-0 text-center'>
                      <i className="fas fa-chevron-up ">
                        <span className='ml-1'>Выберите клетки с поросятами</span>
                      </i>
                    </p>,
                    <ErrorOrMessage error={eventError} message={message} fetching={eventFetching}
                    className='mt-2 mb-0 mx-1 font-15 text-center' />]
                  }
              </div>
              {(activeLocations.length > 0 && expand) &&
                <div>
                  <p className='mb-0 mx-3 font-500 font-14'>Кол-во клеток: {activeLocations.length}</p>
                  <div className='d-flex justify-content-start flex-wrap'>
                    {activeLocations.map(location =>
                      <PigletsVetCellDetail piglets={location.piglets[0]} cell={location.cell}/>
                    )}
                  </div>
                  <CreateMassVetEventForm 
                    initialValues={{
                      date: today,
                    }}
                    recipes={recipes}
                    eventFetching={eventFetching}
                    eventError={eventError}
                    message={message}
                    parentSubmit={this.createMassVetEventPiglets}
                    filterType={this.filterType}
                  />
                </div>
              }
        </div>
      </div>
    </div>
    )
  }
}

export default WSPigletsVetTab